// Dist folder having issues building
// Css from main.scss for nav bar to work 

.grid-container {
    display: -ms-grid;
    display: grid;
    grid-auto-columns: 1fr;
    -ms-grid-columns: 0.6fr 1.2fr 1.2fr;
    grid-template-columns: 0.6fr 1.2fr 1.2fr;
    -ms-grid-rows: 0.5fr 1.5fr 1fr;
    grid-template-rows: 0.5fr 1.5fr 1fr;
    gap: 10px 10px;
    grid-template-areas: "nav header header" "nav main main" "nav main main";
  }
  
  a {
      text-decoration: none;
  }

  nav {
    -ms-grid-row: 1;
    -ms-grid-row-span: 3;
    -ms-grid-column: 1;
    grid-area: nav;
    position: relative;
  }
  
  header {
    -ms-grid-row: 1;
    -ms-grid-column: 2;
    -ms-grid-column-span: 2;
    grid-area: header;
  }
  
  main {
    -ms-grid-row: 2;
    -ms-grid-row-span: 2;
    -ms-grid-column: 2;
    -ms-grid-column-span: 2;
    grid-area: main;
  }
  
  h4 {
    color: black;
    font-family: "Raleway", sans-serif;
  }
  
  #navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 15%;
    height: 100%;
    background: #F6F8FA;
  }
  
  #alltasks {
    margin-left: 30%;
    margin-top: 50px;
    height: 100%;
    width: 50%;
    background-color: #FFFFFF;
    position: absolute;
  }
  
  #selected {
    font-weight: bold;
    font-family: "Raleway", sans-serif;
    color: #034EFC;
  }
  
  h4 {
    color: black;
    font-size: 16px;
    font-family: "Raleway", sans-serif;
    font-weight: lighter;
    margin: 30% 0 75px 15%;
  }
  
  h4:hover {
    color: #034EFC;
  }


h1{
    margin-left: 5%;
    font-size: 100px;
    color: #034EFC;
    font-family: 'Roboto';
}


// Pomodoro styling
main{
    font-family: 'Raleway', sans-serif;
}

h3{
    margin: 15px 0;
    font-weight: 900;
    font-size: 28px;
    text-align: center;
}

input{
    transition:  0.5s ease-in-out;
    margin: 15px 0;
    font-size: 18px;
    padding: 5px;
    background: rgba(0, 0, 0, 0.11);
    border: none;
}

.btn-group{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10%;
    margin: 35px 0;
}

button{
    margin: 5px;
    padding: 10px;
    border: none;
    font-size: 18px;
    transition: 0.5s ease-in-out;
}

form{
    height: 20%;
    display: flex;
    flex-direction: column;
    padding: 0;
    max-width: 370px;
    margin: 5px auto;
}

form button{
    background: rgb(255, 25, 140);
    width: 100%;
    margin: 5px 0;
    color: white;
}

.start{
    background: rgb(48, 33, 253);
    color: white;
}

.break{
    background: rgb(10, 252, 83);
    color: white;
}

.reset{
    background: rgb(255, 51, 51);
    color: white;
}

.pause{
    background: rgb(253, 143, 17);
    color: white;
}

.resume{
    background: rgb(48, 33, 253);
    color: white;
}